/* History Section */
.history-container {
    background: #fff;
    border-radius: 10px;
    padding: 18px;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.05);
    margin-top: 20px;
    transition: all 0.3s ease-in-out;

}

.history-container:hover {
    transform: translateY(-2px);
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.08);
}

.history-button {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/* History Title */
.history-header {
    font-size: 1.6rem;
    font-weight: 700;
    text-align: center;
    margin-bottom: 12px;
    color: #222;
}

/* History List */
.history-list {
    max-height: 250px;
    overflow-y: auto;
    padding-right: 5px;
}

/* Individual Entry */
.history-entry {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #fafafa;
    padding: 12px;
    margin-bottom: 8px;
    border-radius: 6px;
    transition: all 0.3s ease-in-out;
}

.history-entry:hover {
    background: #f1f1f1;
}

/* Delete Button */
.delete-icon {
    cursor: pointer;
    font-size: 18px;
    color: #d33c41;
    transition: 0.3s;
}

.delete-icon:hover {
    color: #ff3b3b;
}

/* Clear History Button */
.clear-history-button {
    width: 100%;
    max-width: 250px;
    padding: 12px;
    font-size: 16px;
    font-weight: 600;
    text-align: center;
    border-radius: 6px;
    border: none;
    background-color: #e74c3c;
    color: white;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.clear-history-button:hover {
    background-color: #c0392b;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
    .history-header {
        font-size: 1.4rem;
    }
}

/* Desktop Media Query */
@media screen and (min-width: 1200px) {
    .history-container {
        max-width: 1200px; /* Increase max-width for larger screens */
        margin: 30px auto; /* Adjust margin for better spacing */
        padding: 24px; /* Increase padding for better spacing */
    }
  
    .history-header {
        font-size: 2rem; /* Increase header size for larger screens */
    }
  
    .history-list {
        max-height: 400px; /* Increase max-height for more visible entries */
    }
  
    .clear-history-button {
        max-width: 300px; /* Increase button width for larger screens */
    }
  }
